/* Media Queries */

@media all and (max-width : 768px) {


	nav ul {
		float: left;
		padding: 0;
		 margin: 0;
		list-style: none;
		position: relative; 
		 left: 0;
		 line-height: 2;
	}

	.topheader ul {
		float: none;
		display: flex;
		padding: 0px 20%;
	}


	.Footer ul li {
		padding: 0px;
	}

	.Footer ul {
		padding: 0px;
	}

	.Footerimg {
		width: 20%;
	}
	
	.socialcss ul li {
		padding: 20px 30px;
	}

	#logo {
		display: block;
		padding: 0;
		width: 100%;
		text-align: center;
		/* float: none; */
	}

	nav {
		margin: 0;
	}

	/* Hide the navigation menu by default */
	/* Also hide the  */
	.toggle + div,
	.menu {
		display: none;
	}

	.bikedropcss {
		position: relative !important;
		left: 0% !important;
		top: 0px;
		width: 60%;
	}


	/* Stylinf the toggle lable */
	.toggle {
		display: block;
		text-align: center;
		padding:14px 20px;	
		color:#FFF;
		font-size:17px;
		text-decoration:none;
		border:none;
	}

	/* .toggle:hover {
		background-color: #000000;
	} */

	[id^=drop]:checked + ul {
		display: block;
	}

	nav ul li {
		display: block;
		width: 100%;
		}

	nav ul ul .toggle,
	nav ul ul a {
		padding: 0 40px;
	}

	nav ul ul ul a {
		padding: 0 80px;
	}

	nav a:hover,
 	nav ul ul ul a {
		background-color: #000000;
	}
  
	nav ul li ul li .toggle,
	nav ul ul a,
  nav ul ul ul a{
		padding:14px 20px;	
		color:#FFF;
		font-size:17px; 
	}
  
  
	nav ul li ul li .toggle,
	nav ul ul a {
		background-color: #212121; 
	}

	/* Hide Dropdowns by Default */
	nav ul ul {
		float: none;
		position:static;
		color: #ffffff;
		/* has to be the same number as the "line-height" of "nav a" */
	}
		
	/* Hide menus on hover */
	nav ul ul li:hover > ul,
	nav ul li:hover > ul {
		display: none;
	}
		
	/* Fisrt Tier Dropdown */
	nav ul ul li {
		display: block;
		width: 100%;
	}

	nav ul ul ul li {
		position: static;
		/* has to be the same number as the "width" of "nav ul ul li" */ 

	}

}

@media all and (max-width : 330px) {

	nav ul li {
		display:block;
		width: 94%;
	}

}

@media all and (max-width : 1440px) {

	nav ul {
	
		position: relative;
		left: 0%;
		
	}

}