#container {
	margin: 0 auto;
	max-width: 890px;
}

p {
	text-align: center;
}

.toggle,
[id^=drop] {
	display: none;
}

/* Giving a background-color to the nav container. */
nav { 
    margin: 0;
    width: 100%;
    /* position: relative;
    left: 20%; */
    padding: 0;
    background-color: #e47045eb;
}

#logo {
    display: block;
    padding: 6px 30px;
    float: left;
    /* font-size: 20px; 
    /* line-height: 59px;*/
}

/* Since we'll have the "ul li" "float:left"
 * we need to add a clear after the container. */

nav:after {
	content:"";
	display:table;
	clear:both;
}

/* Removing padding, margin and "list-style" from the "ul",
 * and adding "position:reltive" */
nav ul {
	float: right;
	padding:0;
	margin:0;
	list-style: none;
    position: relative;
    left: 30%;
    line-height: 3;
	}
	
/* Positioning the navigation items inline */
nav ul li {
	margin: 0px;
    display:inline-block;
    padding: 0px 20px;
	}

/* Styling the links */
nav div {
	display:block;
	/* padding:14px 20px;	 */
	color:#FFF;
	font-size: 13px;
	text-decoration:none;
}

nav div {
	display:block;
	/* padding:14px 20px;	 */
	color:#FFF;
	font-size: 22px;
	text-decoration:none;
}
nav ul li ul li div{font-size: 15px;}

nav ul li ul li:hover { background: #000000; }

/* Background color change on Hover */
nav a:hover { 
	background-color: #000000; 
}

/* Hide Dropdowns by Default
 * and giving it a position of absolute */
nav ul ul {
display: none;
width: 47%;
position: absolute;
top: 60px;
}

.bikedropcss{
    position: absolute !important;
    left: 20% !important;
    }
	
/* Display Dropdowns on Hover */
nav ul li:hover > ul {
	display:inherit;
}
	
/* Fisrt Tier Dropdown */
nav ul ul li {
	width: 125px;
    padding: 0px 10px;
    float: none;
    display: list-item;
    position: relative;
}

/* Second, Third and more Tiers	
 * We move the 2nd and 3rd etc tier dropdowns to the left
 * by the amount of the width of the first tier.
*/
nav ul ul ul li {
	position: relative;
	top:-60px;
	/* has to be the same number as the "width" of "nav ul ul li" */ 
	left:170px; 
}

	
/* Change ' +' in order to change the Dropdown symbol */
li > a:after { content:  ' +'; }
li > a:only-child:after { content: ''; }

